<template>
  <div class="header">
    <el-card>
      <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect" router>
        <el-menu-item index="#" @click="home()"> 首页</el-menu-item>

        <el-submenu index="2">
            <template slot="title">我的工作台</template>
            <template v-for="(item,index) in menu">
              <div v-if="!item.hidden" :key="index">

                <el-submenu v-for="(item,index) in item.children" :index="index + ''"  :key="index" >
                  <template slot="title" >{{item.name}}</template>
                  <template v-for="(item,key) in item.children">
                    <el-menu-item :index="item.path" :key="key">
                      {{item.name}}
                    </el-menu-item>
                  </template>
                </el-submenu>

              </div>
            </template>
        </el-submenu>

        <el-submenu index="3">
          <template slot="title">可视化数据Map</template>
          <el-menu-item index="1-1">微博热搜榜</el-menu-item>
          <el-menu-item index="1-2">树图</el-menu-item>
        </el-submenu>
          <el-menu-item index="mc">我的世界热力图-小游戏</el-menu-item>

        <!--        <el-menu-item index="3" disabled>消息中心</el-menu-item>-->
        <!--        <el-menu-item index="4"><a href="https://www.ele.me" target="_blank">订单管理</a></el-menu-item>-->
      </el-menu>
      <div class="line"></div>

    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      activeIndex: '1',
      activeIndex2: '1',
      menu:[],
    };
  },
  created() {
    this.menu=[...this.$router.options.routes]
  },
  mounted(){  //初始化
      window.name='';
  },
  methods: {
    home(){
      if(window.name===''){
        this.$message({message:'目前正在首页',type:'success'})
      }else {
        this.$router.push('/home')
        this.$message({message:'回到首页',type:'success'})
        window.name='';
      }
    },
    student(){
      this.$router.push('/home/Student')
      this.$message({message:'欢迎访问学生管理系统',type:'success'})
    }
  }
}

</script>


<style lang="scss">
.header {
  width: 100%;
  height: 100%;

  .el-menu--horizontal > .el-menu-item {
    height: 25px;
    line-height: 6px;
  }
  .el-menu--horizontal>.el-submenu .el-submenu__title {
    height: 25px;
    line-height: 0;
  }
}
</style>